.color-picker {
    width: 80%;
    max-width: 700px;
    margin: auto auto;
    background-color: black;
    padding: 20px 0px 20px 0px;
    border-radius: 10px;
    border:black;
    border-style: solid;
    border-width: 2px;
}

.color-swatch-group {
    width: 23px;
    margin: 0px;
    display:inline-block;
    padding: 0px;
}

.swatch {
    background: #fff;
    border-radius: 4px;
    box-shadow: 0 1px 2px #ccc;
    padding: 1px;
    width: 20px;
    height: 20px;
    margin: 0px;
    border-width: 0.1em;
}